import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task6.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";

import chrono1 from './img/chrono_1.png';
import chrono2 from './img/chrono_2.png';
import chrono3 from './img/chrono_3.png';
import chrono4 from './img/chrono_4.png';

import rightAnswer from './img/rightAnswer.png';

// Контент модального окна - Алфавит
const alphabetModalContent = () => {
    return (
        <>
            <h3> Словать</h3>
            <br />
            <p>Много разных слов</p>
        </>
    );
};

// Контент модального окна - Помощь
const helpModalContent = () => {
    return (
        <>
            <h3>Электронный помощник</h3>
            <br />
            <p>Правильный ответ:</p>
            <img src={rightAnswer} alt="" width="100%" />
        </>
    );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const lessonHeading = () => (
    <>
        <p>Восстановление последовательности</p>
    </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const themeHeading = () => (
    <>
        <p style={{ textAlign: 'center' }}>Put the pictures in the right order. Listen to the story and check yourself.
        </p>
    </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = "Диалоговый симулятор";




function Task6() {
    const {
        helpSetIcon,
        alphabetSetIcon,
        helpSetModalContent,
        alphabetSetModalContent,
        lessonSetStartAction,
        lessonSetResetTask,
        resetTask,
        lessonSetCheckTask,
        checkTask,
        lessonSetAndAnswer
    } = useContext(Context);
    // Иконки правого поля  - (помощь и алфавит)
    useEffect(() => {
        //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
        helpSetIcon(true);
        //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);
    const dragPlace = useRef(null);
    const dropPlace = useRef(null);

    const [matchedItem, setMatchedItem] = useState();

    useEffect(() => {
        if (resetTask === true) {
            dropPlace.current.childNodes.forEach(item => {
                if (item.childNodes.length > 1) {
                    dragPlace.current.appendChild(item.childNodes[1]);
                }
            });

        }
        lessonSetResetTask(false);
    }, [resetTask]);

    useEffect(() => {
        if (checkTask === true) {
            const itemsList = new Array();
            dropPlace.current.childNodes.forEach(item => {
                if (item.childNodes.length > 1) {
                    itemsList.push(item.childNodes[1].attributes.getNamedItem('data').value);
                }
            });
            let winCount = 0;
            if (itemsList.length === winLength) {
                dropPlace.current.childNodes.forEach((item, index) => {
                    console.log(item.childNodes[1].attributes.getNamedItem('data').value);
                    if (item.childNodes[1].attributes.getNamedItem('data').value === picturesForMatching[index].data) {
                        winCount++;
                    }
                });
            }
            else {
                lessonSetAndAnswer(false);
            }
            const answer = winCount === picturesForMatching.length ? true : false;
            lessonSetAndAnswer(answer);
        }
        lessonSetCheckTask(false);
    }, [checkTask]);

    const picturesForMatching = [
        {
            id: 'pic_1',
            data: 'chrono1',
            src: chrono1
        },
        {
            id: 'pic_2',
            data: 'chrono2',
            src: chrono2
        },
        {
            id: 'pic_3',
            data: 'chrono3',
            src: chrono3
        },
        {
            id: 'pic_4',
            data: 'chrono4',
            src: chrono4
        }
    ]
    const winLength = picturesForMatching.length;

    let draggingLetter;
    let elemBelow;

    function mouseDown(event) {
        draggingLetter = event.target;
        draggingLetter.style.cursor = 'grabbing';
        let shiftX = event.clientX - draggingLetter.getBoundingClientRect().left;
        let shiftY = event.clientY - draggingLetter.getBoundingClientRect().top;
        draggingLetter.style.position = 'absolute';
        draggingLetter.style.zIndex = 1000;
        document.body.appendChild(draggingLetter);

        moveAt(event.pageX, event.pageY);

        function moveAt(pageX, pageY) {
            draggingLetter.style.left = pageX - shiftX + 'px';
            draggingLetter.style.top = pageY - shiftY + 'px';
        }

        elemBelow = document.elementFromPoint(event.clientX, event.clientY);

        function onMouseMove(event) {

            moveAt(event.pageX, event.pageY);

            draggingLetter.hidden = true;
            elemBelow = document.elementFromPoint(event.clientX, event.clientY);
            draggingLetter.hidden = false;
            if (!elemBelow) return;
        }
        document.addEventListener('mousemove', onMouseMove);


        draggingLetter.onmouseup = function () {
            document.removeEventListener('mousemove', onMouseMove);
            draggingLetter.style.cursor = 'grab';

            if (elemBelow.classList.contains('chronoDropBox') && elemBelow.childNodes.length !== 2) {
                draggingLetter.style.position = 'relative ';
                draggingLetter.style.zIndex = null;
                draggingLetter.style.top = null;
                draggingLetter.style.left = null;
                elemBelow.append(draggingLetter);

                lessonSetStartAction(true);

            }
            else {
                draggingLetter.style.position = 'relative ';
                draggingLetter.style.zIndex = null;
                draggingLetter.style.top = null;
                draggingLetter.style.left = null;
                dragPlace.current.appendChild(draggingLetter);
            }
        };
    };


    const picturesForMatchingReady = useMemo(() => {
        return picturesForMatching.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);



    //  Внесение изменений в контент задания
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                // titleHeading={titleHeading}
                // soundSource={soundSource}
                soundVisible={false}
                titleVisible={false}
            />
            <div className="chronoDndWrapper">
                <div className="cronoDropPlace" ref={dropPlace}>
                    {picturesForMatching.map((picture, index) => (
                        <div key={picture.id} className="chronoDropBox" data={picture.data}><p className="chronoDropBoxNumbers">{index + 1}</p></div>
                    ))}
                </div>
                <div className="chronoDragPlace" ref={dragPlace}>
                    {picturesForMatchingReady.map(picture => (
                        <img key={picture.id} data={picture.data} draggable="false" className="chronoDragPicture" src={picture.src} alt="" onMouseDown={mouseDown} hidden={false} />
                    ))}
                </div>
            </div>

        </div>
    );
}

export default Task6;
