import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task8.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";

import bear from "./img/bear.png"
import elk from "./img/elk.png"
import owl from "./img/owl.png"
import rabbit from "./img/rabbit.png"
import seal from "./img/seal.png"

import bearShadow from "./img/bearShadow.png"
import elkShadow from "./img/elkShadow.png"
import owlShadow from "./img/owlShadow.png"
import rabbitShadow from "./img/rabbitShadow.png"
import sealShadow from "./img/sealShadow.png"


// Контент модального окна - Алфавит
const alphabetModalContent = () => {
    return (
        <>
            <h3> Словать</h3>
            <br />
            <p>Много разных слов</p>
        </>
    );
};

// Контент модального окна - Помощь
const helpModalContent = () => {
    return (
        <>
            <h3>Электронный помощник</h3>
            <br />
            <p></p>

        </>
    );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const lessonHeading = () => (
    <>
        <p>Найди тень</p>
    </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const themeHeading = () => (
    <>
        <p style={{ textAlign: 'center' }}>Соедини животное с его тенью
        </p>
    </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = "Диалоговый симулятор";



function Task5() {
    const {
        helpSetIcon,
        alphabetSetIcon,
        helpSetModalContent,
        alphabetSetModalContent,
        lessonSetStartAction,
        lessonSetResetTask,
        resetTask,
        lessonSetCheckTask,
        checkTask,
        lessonSetAndAnswer,
        lessonSetViewFeedback
    } = useContext(Context);
    // Иконки правого поля  - (помощь и алфавит)
    useEffect(() => {
        //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
        helpSetIcon(true);
        //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);

    const dragPlace = useRef(null);
    const dropPlace = useRef(null);

    const [draggingElem, setDruggingElem] = useState();

    useEffect(() => {
        if (resetTask === true) {
            dropPlace.current.childNodes.forEach(item => {
                if (item.childNodes.length > 1) {
                    dragPlace.current.appendChild(item.childNodes[1]);
                }
            })
            lessonSetResetTask(false);
        }
    }, [resetTask]);

    // useEffect(() => {
    //     if (draggingElem) {
    //         let winVar = 0;
    //         dropPlace.current.childNodes.forEach(item => {
    //             if (item.childNodes.length === 2) {
    //                 winVar++;
    //             }
    //         });
    //         if (winVar === 5) {
    //             lessonSetViewFeedback(true);
    //             lessonSetAndAnswer(true);
    //         }
    //     }
    // }, [draggingElem]);

    useEffect(() => {
        if (checkTask === true) {
            let winVar = 0;
            dropPlace.current.childNodes.forEach(item => {
                if (item.childNodes.length === 2) {
                    winVar++;
                }
            });
            if (winVar === 5) {
                lessonSetAndAnswer(true);
            }
            else lessonSetAndAnswer(false);
        }
        lessonSetCheckTask(false);
    }, [checkTask]);

    const picturesForMatching = [
        {
            id: 'match_3',
            data: 'owl',
            src: owl,
            srcShadow: owlShadow
        },
        {
            id: 'match_4',
            data: 'rabbit',
            src: rabbit,
            srcShadow: rabbitShadow
        },
        {
            id: 'match_2',
            data: 'elk',
            src: elk,
            srcShadow: elkShadow
        },
        {
            id: 'match_5',
            data: 'seal',
            src: seal,
            srcShadow: sealShadow
        },
        {
            id: 'match_1',
            data: 'bear',
            src: bear,
            srcShadow: bearShadow
        },
    ];
    const winVar = 'fire';


    let draggingItem;
    let elemBelow;

    function mouseDown(event) {
        draggingItem = event.target;
        draggingItem.style.cursor = 'grabbing';
        let shiftX = event.clientX - draggingItem.getBoundingClientRect().left;
        let shiftY = event.clientY - draggingItem.getBoundingClientRect().top;

        draggingItem.style.position = 'absolute';
        draggingItem.style.zIndex = 1000;
        document.body.appendChild(draggingItem);

        moveAt(event.pageX, event.pageY);

        function moveAt(pageX, pageY) {
            draggingItem.style.left = pageX - shiftX + 'px';
            draggingItem.style.top = pageY - shiftY + 'px';
        }

        elemBelow = document.elementFromPoint(event.clientX, event.clientY);

        function onMouseMove(event) {

            moveAt(event.pageX, event.pageY);

            draggingItem.hidden = true;
            elemBelow = document.elementFromPoint(event.clientX, event.clientY);
            draggingItem.hidden = false;

            if (!elemBelow) return;
        }
        document.addEventListener('mousemove', onMouseMove);

        // && elemBelow.childNodes.length === 3
        draggingItem.onmouseup = function () {
            document.removeEventListener('mousemove', onMouseMove);
            draggingItem.style.cursor = 'grab';
            // console.log(draggingItem.attributes.getNamedItem('drag-data').value)
            // console.log(elemBelow.attributes.getNamedItem('drop-data').value)
            if (elemBelow.classList.contains('dragPicture_animalShadow') && elemBelow.attributes.getNamedItem('drop-data').value === draggingItem.attributes.getNamedItem('drag-data').value) {


                draggingItem.style.position = 'relative ';
                draggingItem.style.zIndex = null;
                draggingItem.style.top = null;
                draggingItem.style.left = null;
                elemBelow.parentElement.append(draggingItem);

                lessonSetStartAction(true);

            }
            else {
                draggingItem.style.position = 'relative ';
                draggingItem.style.zIndex = null;
                draggingItem.style.top = null;
                draggingItem.style.left = null;
                dragPlace.current.appendChild(draggingItem);
            }
            setDruggingElem(draggingItem);
        };
    };

    const picturesForMatchingReady = useMemo(() => {
        return picturesForMatching.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);



    //  Внесение изменений в контент задания
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                // titleHeading={titleHeading}
                // soundSource={soundSource}
                soundVisible={false}
                titleVisible={false}
            />
            <div className="comparePicturesWrapper" >
                <div className="dropPlaceWrapper_animals" ref={dropPlace}>
                    {picturesForMatching.map((picture => (
                        <div key={picture.id} className="dropPlace_animal"  ><img className="dragPicture_animalShadow" src={picture.srcShadow} alt="" drop-data={picture.data} draggable="false" /></div>
                    )))}
                </div>
                <div className="dragPlaceWrapper_animals" ref={dragPlace}>
                    {picturesForMatchingReady.map((picture => (
                        <img key={picture.id} className="dragPicture_animal" src={picture.src} alt="" onMouseDown={mouseDown} draggable="false" drag-data={picture.data} />
                    )))}
                </div>
            </div>

        </div>
    );
}

export default Task5;
