import React, { useState, useContext, useEffect, useRef, useMemo } from "react";
import "./Task7.css";
import { Context } from "../../Context";
import { HeadingText } from "../HeadingText/HeadingText";

import toy from "./img/toy.png";
import fire from "./img/fire.png";
import mark from "./img/mark.png";

// Контент модального окна - Алфавит
const alphabetModalContent = () => {
    return (
        <>
            <h3> Словать</h3>
            <br />
            <p>Много разных слов</p>
        </>
    );
};

// Контент модального окна - Помощь
const helpModalContent = () => {
    return (
        <>
            <h3>Электронный помощник</h3>
            <br />
            <p></p>

        </>
    );
};

// Изменение заголовка (поддерживаеи html тэги)
// START
// Верхний заголовок - название урока ( <> текст размещать внутри тэгов </> )
const lessonHeading = () => (
    <>
        <p>Единичный выбор ответа</p>
    </>
);
// Заголовок с заданием к уроку ( <> текст размещать внутри тэгов </> )
const themeHeading = () => (
    <>
        <p style={{ textAlign: 'center' }}>Чаще всего пожары случаются из-за неправильного поведения людей.<br />Посмотри на картинки. Отметь ту, на которой поведение людей может привести к пожару.
        </p>
    </>
);
// Текс title с переводом (НЕ поддерживает html тэги)
const titleHeading = "Диалоговый симулятор";

const picturesForMatching = [
    {
        id: 'mark_1',
        data: 'toy',
        src: toy
    },
    {
        id: 'mark_2',
        data: 'fire',
        src: fire
    }
];
const winVar = 'fire';

const PicturesForMatching = ({ picturesForMatchingReady, clickHandler }) => (
    <>
        {picturesForMatchingReady.map((picture, index) => (
            <div key={picture.id} className="oneChoiseMark_picture" onClick={clickHandler} >
                <img className="markPic markNone" src={mark} alt="" data={picture.data} />
                <img className="pictureForMark" src={picture.src} alt="" />
            </div>
        ))}
    </>

);

function Task7() {
    const {
        helpSetIcon,
        alphabetSetIcon,
        helpSetModalContent,
        alphabetSetModalContent,
        lessonSetStartAction,
        lessonSetResetTask,
        resetTask,
        lessonSetCheckTask,
        checkTask,
        lessonSetAndAnswer
    } = useContext(Context);
    // Иконки правого поля  - (помощь и алфавит)
    useEffect(() => {
        //  helpSetIcon - иконка электронного помощника (включить - true, отключить - false)
        helpSetIcon(true);
        //  alphabetSetIcon - иконка алфавита (включить - true, отключить - false)
        alphabetSetIcon(true);
        helpSetModalContent(helpModalContent);
        alphabetSetModalContent(alphabetModalContent);
    }, []);

    const oneChoiseMark = useRef();

    const [markToy, setMarkToy] = useState(false);
    const [markFire, setMarkFire] = useState(false);

    const [choise, setChoise] = useState();



    useEffect(() => {
        if (resetTask === true) {
            oneChoiseMark.current.childNodes.forEach(item => {
                item.childNodes[0].classList.add('markNone')
            });
        }
        lessonSetResetTask(false);
    }, [resetTask]);

    useEffect(() => {
        if (checkTask === true) {
            if (choise.childNodes[0].attributes.getNamedItem('data').value === winVar) {
                lessonSetAndAnswer(true);
            }
            else lessonSetAndAnswer(false);

        }
        lessonSetCheckTask(false);
    }, [checkTask]);


    const picturesForMatchingReady = useMemo(() => {
        return picturesForMatching.sort(() => Math.random() > 0.5 ? 1 : -1);
    }, []);

    const clickHandler = (event) => {
        oneChoiseMark.current.childNodes.forEach(item => {
            item.childNodes[0].classList.add('markNone')
        });
        event.target.childNodes[0].classList.remove('markNone');
        lessonSetStartAction(true);
        setChoise(event.target)
    }

    //  Внесение изменений в контент задания
    return (
        <div className="lessonContent">
            <HeadingText
                lessonHeading={lessonHeading()}
                themeHeading={themeHeading()}
                // titleHeading={titleHeading}
                // soundSource={soundSource}
                soundVisible={false}
                titleVisible={false}
            />
            <div className="oneChoiseMark" ref={oneChoiseMark}>
                <PicturesForMatching picturesForMatchingReady={picturesForMatchingReady} clickHandler={clickHandler} />
            </div>

        </div>
    );
}

export default Task7;
